@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {box-sizing: border-box;}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.login {
  display: flex;
  align-items: center;
  background-color: black;
  height: 100vh;
  background: #a8edea;
  background: linear-gradient(to right, #a8edea 0%, #fed6e3 100%);
}

.login-container {
  background-color: rgb(255, 255, 255);
  margin: auto;
  border-radius: 20px;
  box-shadow: -1px 21px 42px 0px rgba(0, 0, 0, 0.05);
  -webkit-box-shadow: -1px 21px 42px 0px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: -1px 21px 42px 0px rgba(0, 0, 0, 0.05);
  display: flex;
}

.loginimg {
  width: 600px;
  height: 600px;
  padding: 50px;
  background-image: url(/my-app/static/media/loginimg.8581576a.jpg);
  border-radius: 20px 0px 0px 20px;
  object-fit: fill;
  background-color: rgb(39, 160, 190);
  background-blend-mode: multiply;
}

.loginimg h1 {
  font-size: 48px;
  font-weight: 300;
  color: #ffffff;
  line-height: 1.4;
}

.loginimg p {
    font-size: 20px;
    font-weight: 500;
    color: #ffffff;
    line-height: 1.4;
  }

.form-area {
  width: 600px;
  height: 600px;
  background-color: rgb(255, 255, 255);
  border-radius: 20px;
  padding: 70px;
}

.logo {
  font-family: "Rubik", sans-serif;
  font-size: 32px;
  color: #1f306e;
  font-weight: bolder;
}

.logo span {
  color: #8f3b76;
  font-weight: bolder;
  font-weight: bolder;
}

.form-container{
    margin: 40px 0px 10px 0px;
}

.form-container h2{
    font-weight: bold;
    color: #666666;
    margin: 0;
}

.form{
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.primarybtn{
    padding: 15px 35px;
    background-color: #f5487f;
    color: #ffffff;
    font-size: 16px;
    font-weight: 600;
    border-radius: 4px;
    border: none;
    cursor: pointer;
    transition: .5s;
}

.primarybtn:hover{
    background-color: #c7417b;
    border: none;
}

.form label{
    font-size: 16px;
    color: #444444;
    margin: 20px 0px 10px 0px;
}

.form input{
    width: 100%;
    height: 60px;
    background-color: rgb(244, 244, 244);
    border: none;
    border-radius: 4px;
    padding: 20px;
    font-size: 16px;
    color: #444444;
}
